/* [loading] layout.css이나 reset.css 둘중 아무 파일 하단에 추가  */
.loading_tb_w{z-index:100000;position:fixed;top:0;left:0;width:100%;height:100%;}
.loading_tb{display:table;width:100%;height:100%;}
.loading_td{display:table-cell;vertical-align: middle;text-align:center;}
.loader {
  border: 3px solid #fff;
  border-radius: 50%;
  border-top: 3px solid #511bb4;
  width: 60px;
  height: 60px;
  
  margin:0 auto;
  -webkit-animation: spin 1.1s linear infinite; /* Safari */
  animation: spin 1.1s linear infinite;
}
.loader.type2{
  border-width: 2px;
  width: 40px;
  height: 40px;
}

/* Safari */
@-webkit-keyframes spin {
  0% { -webkit-transform: rotate(0deg); }
  100% { -webkit-transform: rotate(360deg); }
}
@keyframes spin {
  0% { transform: rotate(0deg); }
  100% { transform: rotate(360deg); }
}
