.front_body{background:#f2f2f2;}
.page_wrap{min-width:1280px;background:#f2f2f2;}
.page_wrap.page_layout{
  .bar{
    position: absolute;
    left: 0;
    width: 22px;
    height: 2px;
    background: #333;
    transform: rotate(0);
    &:nth-child(1){
      top: 0;
      transform-origin: 0 0;
    }
    &:nth-child(2){
      top: 8px;
    }
    &:nth-child(3){
      top: 16px;
      transform-origin: 0 100%;
    }
  }
  &.fold{
    .bar{
      &:nth-child(1){
        top: 1px;
        left: 3px;
        transform: rotate(45deg);
      }
      &:nth-child(2){
        opacity: 0;
      }
      &:nth-child(3){
        left: 3px;
        transform: rotate(-45deg);
      }
    }
  }
}
.page_wrap.page_layout:after{display:block;content:'';clear:both;}
